@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{font-size:16px}.app{width:100%}.app .fetching-spinner-wrapper{position:absolute;top:70px;margin:auto;width:100%;height:5px;position:relative}.app .app-content{margin:auto;box-sizing:border-box;margin-top:70px;width:100%;max-width:1200px;box-shadow:1px 1px 10px 2px #00000060;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;font-family:'Montserrat', sans-serif}@media (max-width: 992px){html{font-size:12px}.app .app-content{max-width:none}}

.header-wrapper{width:100%;height:40px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;position:fixed;font-family:'Montserrat', sans-serif;top:0;left:0;z-index:100;background-color:#00754e;padding:10px;color:#fff}.header-wrapper .header-icon{font-size:1.8rem;margin-right:5px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.header-wrapper .header-title{font-size:1.3rem;margin:0}

.contact-item-row{font-weight:bolder;color:#222222;cursor:pointer}.contact-item-row:hover{background-color:#e2e2e2}.contact-item-row:focus{outline:none;background-color:#ccc}.contact-item-row .avatar{width:80px}.contact-item-row .avatar .avatar-wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:55px;height:55px;background-color:#00754e;box-shadow:1px 1px 5px 1px #00000080;color:#fff;border-radius:2px;border-radius:50%;box-sizing:border-box;overflow:hidden}.contact-item-row .avatar .avatar-wrapper .avatar-img{width:100%;height:auto;object-fit:cover}.contact-item-row .avatar .avatar-wrapper svg{font-size:1.4rem}.contact-item-row .select-contact{text-align:right;cursor:pointer}.contact-item-row .select-contact .checkbox{border:none;width:25px;height:25px;border-radius:2px;color:white;margin-left:auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#2d6955;font-size:3rem;padding:0;cursor:pointer}

.contacts-list-table{width:100%;text-align:left;border-collapse:collapse;border-radius:1px;border-spacing:0 10px}.contacts-list-table th:nth-child(3){text-align:right}.contacts-list-table td,.contacts-list-table th{padding:0.5rem 1rem}.contacts-list-table td{border-bottom:1px solid #b9b9b9}

.contacts-navigation-wrapper{width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:1rem;margin:10px 0;box-sizing:border-box}.contacts-navigation-wrapper .search-input-wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;border:2px solid #00754e;border-radius:25px;padding:8px;color:#313131;font-size:1.1rem}.contacts-navigation-wrapper .search-input-wrapper .search-input-icon{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin:0 5px;color:#00754e}.contacts-navigation-wrapper .search-input-wrapper .search-input{border:none;font-weight:bolder;color:#313131}.contacts-navigation-wrapper .search-input-wrapper .search-input:focus{outline:none}.contacts-navigation-wrapper .pagination-wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.contacts-navigation-wrapper .pagination-wrapper .prev-page-button,.contacts-navigation-wrapper .pagination-wrapper .next-page-button{border:none;padding:5px 15px;background-color:#00754e;border-radius:2px;margin-left:5px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;color:#fff;box-shadow:1px 1px 5px 1px #00000060;cursor:pointer}.contacts-navigation-wrapper .pagination-wrapper .prev-page-button .button-icon,.contacts-navigation-wrapper .pagination-wrapper .next-page-button .button-icon{font-size:1.5rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.contacts-navigation-wrapper .pagination-wrapper .prev-page-button:focus,.contacts-navigation-wrapper .pagination-wrapper .next-page-button:focus{outline:none}.contacts-navigation-wrapper .pagination-wrapper .prev-page-button:hover,.contacts-navigation-wrapper .pagination-wrapper .next-page-button:hover{background-color:#11805b}.contacts-navigation-wrapper .pagination-wrapper .prev-page-button:disabled,.contacts-navigation-wrapper .pagination-wrapper .next-page-button:disabled{background-color:#315f51}

